"use client";

import { useState, useEffect } from "react";
import Link from "next/link";
import Image from "next/image";
import { usePathname } from "next/navigation";

export default function Navbar() {
    const [isSticky, setIsSticky] = useState(false);
    const [isMobileMenuOpen, setIsMobileMenuOpen] = useState(false);
    const pathname = usePathname();

    useEffect(() => {
        const onScroll = () => {
            // Add sticky effect after scrolling 50px
            setIsSticky(window.scrollY > 50);
        };
        window.addEventListener("scroll", onScroll);
        return () => window.removeEventListener("scroll", onScroll);
    }, []);

    // Common link styles
    const linkClasses = (path: string) =>
        `text-sm font-bold tracking-wide transition-colors duration-200 hover:text-blue-600 ${
            pathname === path ? "text-blue-600" : "text-gray-700"
        }`;

    return (
        <header
            className={`fixed top-0 left-0 right-0 z-50 transition-all duration-300 ${
                isSticky ? "py-2" : "py-6"
            }`}
        >
            <div
                className={`mx-auto px-4 sm:px-6 transition-all duration-300 ${
                    isSticky ? "max-w-full rounded-none" : "max-w-7xl"
                }`}
            >
                {/* The "Container" - using backdrop blur for glass effect */}
                <div
                    className={`relative bg-white/80 backdrop-blur-md border border-white/20 shadow-xl overflow-hidden transition-all duration-300 ${
                        isSticky ? "rounded-none shadow-sm" : "rounded-2xl"
                    }`}
                >
                    {/* Decorative "Specular" gradient effect */}
                    <div className="absolute inset-0 bg-gradient-to-b from-white/40 to-transparent pointer-events-none" />

                    <div className="relative flex items-center justify-between px-6 py-3">
                        {/* Logo Section */}
                        <Link href="/" className="flex items-center gap-3 group">
                            <div className="relative overflow-hidden rounded-full shadow-sm group-hover:scale-105 transition-transform">
                                <Image
                                    src="https://sharesport.news/logo/1675071036-logo.jpg"
                                    alt="Share Sport Logo"
                                    width={40}
                                    height={40}
                                    className="object-cover"
                                />
                            </div>
                            <h2 className="text-xl font-black bg-clip-text text-transparent bg-gradient-to-r from-blue-700 to-blue-500">
                                Share Sport
                            </h2>
                        </Link>

                        {/* Desktop Navigation */}
                        <nav className="hidden md:flex items-center gap-8">
                            <Link href="/" className={linkClasses("/")}>HOME</Link>
                            <Link href="/livescore" className={linkClasses("/livescore")}>LIVESCORE</Link>
                            <Link href="/livestream" className={linkClasses("/livestream")}>LIVESTREAM</Link>
                            <Link href="/news" className={linkClasses("/news")}>NEWS</Link>
                            <Link href="/highlight" className={linkClasses("/highlight")}>HIGHLIGHTS</Link>
                        </nav>

                        {/* Mobile Menu Button */}
                        <button
                            className="md:hidden p-2 text-gray-700 rounded-lg hover:bg-gray-100"
                            onClick={() => setIsMobileMenuOpen(!isMobileMenuOpen)}
                            aria-label="Toggle mobile menu"
                        >
                            <svg
                                xmlns="http://www.w3.org/2000/svg"
                                fill="none"
                                viewBox="0 0 24 24"
                                strokeWidth={1.5}
                                stroke="currentColor"
                                className="w-6 h-6"
                            >
                                {isMobileMenuOpen ? (
                                    <path strokeLinecap="round" strokeLinejoin="round" d="M6 18L18 6M6 6l12 12" />
                                ) : (
                                    <path strokeLinecap="round" strokeLinejoin="round" d="M3.75 6.75h16.5M3.75 12h16.5m-16.5 5.25h16.5" />
                                )}
                            </svg>
                        </button>
                    </div>

                    {/* Mobile Navigation Dropdown */}
                    {isMobileMenuOpen && (
                        <div className="md:hidden bg-white/95 backdrop-blur-xl border-t border-gray-100">
                            <nav className="flex flex-col p-4 space-y-4">
                                <Link href="/" className={linkClasses("/")} onClick={() => setIsMobileMenuOpen(false)}>HOME</Link>
                                <Link href="/livescore" className={linkClasses("/livescore")} onClick={() => setIsMobileMenuOpen(false)}>LIVESCORE</Link>
                                <Link href="/livestream" className={linkClasses("/livestream")} onClick={() => setIsMobileMenuOpen(false)}>LIVESTREAM</Link>
                                <Link href="/news" className={linkClasses("/news")} onClick={() => setIsMobileMenuOpen(false)}>NEWS</Link>
                                <Link href="/highlight" className={linkClasses("/highlight")} onClick={() => setIsMobileMenuOpen(false)}>HIGHLIGHTS</Link>
                            </nav>
                        </div>
                    )}
                </div>
            </div>
        </header>
    );
}